import * as React from 'react';
import {useState} from 'react';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Language from '@mui/icons-material/Language';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import {createTheme, ThemeProvider} from '@mui/material/styles';
import {Copyright} from "./Copyright";
import vocab from './vocabulary/vocabulary.json'

const theme = createTheme();

//Tiempos
const PERFECTO = 'Perfecto'
const INDEFINIDO = 'Indefinido'
const IMPERFECTO = 'Imperfecto'

//Groups
const Group = {
  "PERFECTO": {"key": 'PERFECTO', "name": 'Perfecto Regular', "tiempo": PERFECTO},
  "INDEFINIDO_AR": {"key": 'INDEFINIDO_AR', "name": 'Indefinido Regular -ar', "tiempo": INDEFINIDO},
  "INDEFINIDO_ER_IR": {"key": 'INDEFINIDO_ER_IR', "name": 'Indefinido Regular -er/-ir', "tiempo": INDEFINIDO},
  "IMPERFECTO_AR": {"key": 'IMPERFECTO_AR', "name": 'Imperfecto Regular -ar', "tiempo": IMPERFECTO},
  "IMPERFECTO_ER_IR": {"key": 'IMPERFECTO_ER_IR', "name": 'Imperfecto Regular -er/-ir', "tiempo": IMPERFECTO}
}

const enabledGroups = [Group.PERFECTO, Group.INDEFINIDO_AR, Group.INDEFINIDO_ER_IR, Group.IMPERFECTO_AR, Group.IMPERFECTO_ER_IR]

const vocabByGroup = {
  [Group.PERFECTO.key]: findVocabPerfecto(),
  [Group.INDEFINIDO_AR.key]: findVocabIndefinido('ar'),
  [Group.INDEFINIDO_ER_IR.key]: findVocabIndefinido('er').concat(findVocabIndefinido('ir')),
  [Group.IMPERFECTO_AR.key]: findVocabImperfecto('ar'),
  [Group.IMPERFECTO_ER_IR.key]: findVocabImperfecto('er').concat(findVocabImperfecto('ir'))
}

function findVocabPerfecto() {
  return Object.entries(vocab).filter(([word, desc]) => {
    return !word.endsWith('se') && desc.comoGustar == null && desc.perfecto?.group == null
  }).map(el => el[0]);
}

function findVocabIndefinido(end) {
  return Object.entries(vocab).filter(([word, desc]) => {
    return word.endsWith(end) && desc.comoGustar == null && desc.indefinido?.group == null
  }).map(el => el[0]);
}

function findVocabImperfecto(end) {
  return Object.entries(vocab).filter(([word, desc]) => {
    return word.endsWith(end) && desc.comoGustar == null && desc.imperfecto?.group == null
  }).map(el => el[0]);
}

const personas = ['Yo', 'Tu', 'El', 'Nosotros', 'Vosotros', 'Ellos']

function findHint(tiempo, verbo, persona) {
  if (tiempo === 'Indefinido') {
    return indefinido(verbo, persona)
  } else if (tiempo === 'Imperfecto') {
    return imperfecto(verbo, persona)
  } else if (tiempo === 'Perfecto') {
    return perfecto(verbo, persona)
  }
}

function indefinido(verbo, persona) {
  const base = verbo.slice(0, -2)
  if (verbo.endsWith('ar')) {
    const ends = ['é', 'aste', 'ó', 'amos', 'asteis', 'aron']
    return base + ends[persona]
  } else {
    const ends = ['í', 'iste', 'ió', 'imos', 'isteis', 'ieron']
    return base + ends[persona]
  }
}

function imperfecto(verbo, persona) {
  const base = verbo.slice(0, -2)
  if (verbo.endsWith('ar')) {
    const ends = ['aba', 'abas', 'aba', 'ábamos', 'abais', 'aban']
    return base + ends[persona]
  } else {
    const ends = ['ía', 'ías', 'ía', 'íamos', 'íais', 'ían']
    return base + ends[persona]
  }

}

function perfecto(verbo, persona) {
  const haber = ['he', 'has', 'ha', 'hemos', 'habéis', 'han']
  return haber[persona] + ' ' + participio(verbo)

}

function participio(verbo) {
  const base = verbo.slice(0, -2)
  if (verbo.endsWith('ar')) {
    return base + 'ado'
  } else {
    return base + 'ido'
  }
}

function getRandomInt(max) {
  return Math.floor(Math.random() * max);
}

function shuffleArray(array) {
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
}

function randomCombinations() {
  const combinations = []
  enabledGroups.forEach(group => {
    for (let index in personas) {
      combinations.push([group, index])
    }
  })
  shuffleArray(combinations)
  return combinations
}

function nextState(currState) {
  let combinations = [...currState.combinations]
  if (combinations.length === 0) {
    combinations = randomCombinations()
  }
  const [group, persona] = combinations.pop()

  const vocab = vocabByGroup[group.key]
  return {
    "combinations": combinations,
    "tiempo": group.tiempo,
    "verbo": vocab[getRandomInt(vocab.length)],
    "persona": persona,
  }
}

function capitalize(string) {
  return string.charAt(0).toUpperCase() + string.slice(1);
}

export default function SignIn() {

  const [state, setState] = useState(() => nextState({"combinations": []}))
  const [hint, setHint] = useState('')

  function findHintFor(state) {
    return findHint(state.tiempo, state.verbo, state.persona)
  }

  function onClickNext() {
    setHint('')
    setState(nextState)
  }

  function onClickHint() {
    setHint(findHintFor(state))
  }

  function getProgress() {
    const total = enabledGroups.length * personas.length
    return total - state.combinations.length + '/' + total
  }

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline/>
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Grid container spacing={2}>
            <Grid container justifyContent="center" item xs={12}>
              <Language fontSize="large" color="secondary"/>
              <Typography component="h1" variant="h5">
                Conjugador
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <TextField id="tiempo-text" fullWidth label="Tiempo" value={state.tiempo} InputProps={{readOnly: true,}}/>
            </Grid>
            <Grid item xs={5}>
              <TextField id="persona-text" label="Persona" value={personas[state.persona]}
                         InputProps={{readOnly: true,}}/>
            </Grid>
            <Grid item xs={7}>
              <TextField id="verbo-text" fullWidth label="Verbo" value={capitalize(state.verbo)}
                         InputProps={{readOnly: true,}}/>
            </Grid>
            <Grid container item xs={10}>
              <Typography variant="h6">Hint: {capitalize(hint)}</Typography>
            </Grid>
            <Grid container item xs={2}>
              <Typography
                variant="h6">{getProgress()}</Typography>
            </Grid>
            <Grid item xs={5}>
              <Button type="submit" fullWidth variant="contained" onClick={onClickHint}>Hint</Button>
            </Grid>
            <Grid item xs={7}>
              <Button type="submit" fullWidth variant="contained" onClick={onClickNext}>Next</Button>
            </Grid>
          </Grid>
        </Box>
        <Copyright sx={{mt: 4, mb: 4}}/>
      </Container>
    </ThemeProvider>
  );
}